.selection {
  padding: 1rem;
  background-color: var(--color-off-white);
  border-radius: 100%;
  border: none;
  color: var(--color-navy);
  font-size: 1.5rem;
  font-weight: bold;
  position: relative;
  cursor: pointer;
  min-height: 4rem;
  min-width: 4rem;

  &__selected {
    background-color: var(--color-dark-navy);
    color: var(--color-off-white);
  }

  &:hover {
    &::after {
      content: "";
      height: 100%;
      width: 100%;
      border: 1.5px solid var(--color-gray);
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 100%;
      transform: scale(1.2);
    }
  }

  &__tick {
    opacity: 0;
    transition: all 0.2s;
    background-color: transparent;
  }

  &__selected-color {
    opacity: 1;
  }
  &__selected-font {
    background-color: var(--color-navy);
    color: var(--color-off-white);
  }
}
