.input {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
  max-width: 14rem;

  &__label {
    font-weight: bold;
    font-size: 1.2rem;
    color: var(--color-navy);
  }

  &__input {
    font-size: 1.4rem;
    font-weight: bold;
    color: var(--color-navy);
    padding: 1.5rem 0 1.5rem 1rem;
    background-color: var(--color-off-white);
    border: none;
    border-radius: 10px;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    &:active,
    &:focus {
      outline: none;
    }
  }

  &__arrow-group {
    position: absolute;
    right: 1rem;
    bottom: 1.1rem;
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
  }

  &__arrow {
    cursor: pointer;
    opacity: 0.4;
    &:hover {
      opacity: 1;
    }
  }
}
