.button {
  color: var(--color-off-white);
  padding: 1.5rem 4.8rem;
  font-size: 1.6rem;
  border-radius: 50px;
  border: none;
  background-color: var(--color-red);
  transition: all 0.2s;
  cursor: pointer;
  position: relative;
  &:hover {
    filter: brightness(1.5);
  }

  &:active {
    transform: translateX(-50%) scale(0.95);
  }
}
