.settings {
  position: absolute;
  top: 13rem;
  z-index: 10;
  display: flex;
  flex-direction: column;
  border-radius: 50px;
  background-color: var(--color-white);
  padding: 5rem;
  gap: 3rem;

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 9;
    background-color: rgba(0, 0, 0, 50%);
  }

  &__header-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__header {
    color: var(--color-navy);
  }
  &__close {
    cursor: pointer;
  }
  &__sub-heading {
    color: var(--color-navy);
    flex: 1;
  }
  &__input-group {
    display: flex;
    gap: 2rem;
    @media screen and (max-width: 600px) {
      flex-direction: column;
    }
  }
  &__selector-group {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  &__button {
    position: absolute;
    bottom: -2.4rem;
    left: 50%;
    transform: translateX(-50%);
  }

  &__divider {
    background-color: var(--color-gray);
    height: 1px;
    border: none;

    &--full {
      width: calc(100% + 10rem);
      margin-left: -5rem;
    }
  }
}
