@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&family=Roboto+Slab:wght@400;700&family=Space+Mono:wght@400;700&display=swap");

:root {
  //Themes
  --color-red: #f87070;
  --color-teal: #70f3f8;
  --color-pink: #d881f8;
  --selected-theme: var(--color-red);
  //Colors
  --color-gray: #d7e0ff;
  --color-white: #fff;
  --color-off-white: #eff1fa;
  --color-navy: #1e213f;
  --color-dark-navy: #161932;

  //Progress
  --percentage: 0;

  //Fonts
  --font-1: "Roboto Slab", serif;
  --font-2: "Space Mono", monospace;
  --font-3: "Kumbh Sans", sans-serif;
  --selected-font: var(--font-3);
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;

  @media screen and(max-width: 500px) {
    font-size: 55%;
  }
}

body {
  box-sizing: border-box;
  background-color: var(--color-navy);
  font-family: var(--selected-font);
}

h1,
h2,
h3,
h4,
p,
span {
  font-weight: bold;
  color: var(--color-off-white);
}

h1 {
  font-size: 10rem;
  line-height: 12rem;
  letter-spacing: -0.05rem;
}

h2 {
  font-size: 2.8rem;
  line-height: 3.4rem;
}

h3 {
  font-size: 1.6rem;
  line-height: 1.9rem;
  text-transform: uppercase;
  letter-spacing: 1.5rem;
}

h4 {
  font-size: 1.3rem;
  line-height: 1.6rem;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

p {
  font-size: 1.4rem;
  line-height: 1.8rem;
}

span {
  font-size: 1.2rem;
  line-height: 1.4rem;
}
