.progress {
  background-color: var(--color-dark-navy);
  display: flex;
  gap: 1rem;
  padding: 0.8rem;
  border-radius: 30px;
  position: relative;
  align-items: center;

  &__span {
    content: "";
    position: absolute;
    background-color: var(--selected-theme);
    color: var(--color-navy);
    width: 33%;
    height: 84%;
    border-radius: 30px;
    top: 8%;
    z-index: 1;
    transition: all 0.45s;

    &--left {
      right: 65%;
    }

    &--middle {
      right: 33%;
    }

    &--right {
      right: 0.6rem;
    }
  }

  &__text {
    z-index: 2;
    position: relative;
    color: currentColor;
    transition: all 0.4s;
  }

  &__section {
    &,
    &:active,
    &:focus {
      border: none;
      background-color: transparent;
    }
    padding: 1.5rem 2rem;
    border-radius: 30px;
    position: relative;
    color: var(--color-gray);

    &--selected {
      color: var(--color-navy);
    }
  }
}
