.timer {
  height: 41rem;
  width: 41rem;
  background-image: linear-gradient(100deg, var(--color-dark-navy), 30%, var(--color-navy));
  border-radius: 100%;
  position: relative;
  box-shadow: 30px 30px 50px 10px rgba(22, 25, 50, 75%), -30px -30px 50px 10px rgba(44, 49, 94, 0.5);
  cursor: pointer;

  &__inner {
    position: absolute;
    height: 37rem;
    width: 37rem;
    background-color: var(--color-dark-navy);
    border-radius: 100%;
    top: 2rem;
    left: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
  }
  &__progress {
    transition: all 1s;
    background-image: conic-gradient(var(--selected-theme) var(--percentage-progress), var(--color-dark-navy) var(--percentage-mask));
    height: 36rem;
    width: 36rem;
    position: absolute;
    top: 0.5rem;
    left: 0.5em;
    border-radius: 100%;
    z-index: 1;
  }

  &__mask {
    background-color: var(--color-dark-navy);
    height: 34rem;
    width: 34rem;
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    border-radius: 100%;
    z-index: 4;
    box-shadow: inset 30px 30px 50px 10px rgba(22, 25, 50, 10%), inset -30px -30px 50px 10px rgba(44, 49, 94, 10%);
  }
  &__time {
    z-index: 5;
    min-width: 27rem;
    margin-left: 0.5rem;
  }
  &__status {
    z-index: 5;
  }
}
